import React from "react";
import styled from "styled-components";
import { FaInstagram, FaTwitter, FaFacebookF, FaHeart } from "react-icons/fa";
import { BodyIntro, Caption, Caption2, H3 } from "../styles/TextStyles";

export default function Footer() {
    return (
        <Wrapper>
            <FooterTop>
                <div>
                    <H3>Let's engineer great products Together</H3>
                    <br />
                    <Caption2>
                        <del>Estimate your project</del>
                    </Caption2>
                </div>
                <div>
                    <AdressItem>Bluetec, 2nd floor Makelele house</AdressItem>
                    <AdressItem>Av. Songa Q.Asiatique</AdressItem>
                    <AdressItem>Bujumbura, Burundi</AdressItem>
                    <br />
                    <AdressItem>00257 77502604</AdressItem>
                    <AdressItem>00257 62115300</AdressItem>
                </div>
                <div>
                    <BodyIntro>Our Projects</BodyIntro>
                    <ProjectItem>
                        <img src="/images/icons/courses.svg" alt="courses" />
                        <span>Project one</span>
                    </ProjectItem>
                    <ProjectItem>
                        <img src="/images/icons/courses.svg" alt="courses" />
                        <span>Project Two</span>
                    </ProjectItem>
                    <ProjectItem>
                        <img src="/images/icons/courses.svg" alt="courses" />
                        <span>Project Three</span>
                    </ProjectItem>
                </div>
            </FooterTop>
            <FooterBottom>
                <IconsWrapper>
                    <Link href="https://instagram.com">
                        <FaInstagram />
                    </Link>
                    <Link href="https://twitter.com">
                        <FaTwitter />
                    </Link>
                    <Link href="https://facebook.com">
                        <FaFacebookF />
                    </Link>
                </IconsWrapper>
                <div>
                    Made with by <FaHeart color="red" size={10} /> Bluetec
                </div>
            </FooterBottom>
        </Wrapper>
    );
}

const Wrapper = styled.footer`
    color: #fff;
    max-width: 1234px;
    margin: 0 auto;
    padding: 150px 30px 60px;
`;

const FooterTop = styled.div`
    display: grid;
    grid-template-columns: 45% 25% 20%;
    justify-content: space-between;
    @media (max-width: 768px) {
        grid-template-columns: 100%;
        gap: 30px;
    }
`;
const FooterBottom = styled.div`
    display: grid;
    grid-template-columns: 45% 25% 20%;
    justify-content: space-between;
    padding-top: 50px;
    @media (max-width: 768px) {
        grid-template-columns: 100%;
        gap: 30px;
    }
`;

const IconsWrapper = styled.div`
    display: flex;
    gap: 15px;
`;

const Link = styled.a`
    text-decoration: none;
    color: #1f1f47;
    border-radius: 50%;
    padding: 8px;
    background: white;
`;

const AdressItem = styled(Caption)`
    font-weight: normal;
`;

const ProjectItem = styled.div`
    display: flex;
    width: fit-content;
    gap: 10px;
    align-items: center;
    padding: 8px;
    transition: 0.5s ease-out;
    border-radius: 10px;
    color: rgba(255, 255, 255, 0.9);
    cursor: pointer;
    :hover {
        background: rgba(255, 255, 255, 0.1);
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1),
            inset 0px 0px 0px 0.5px rgba(255, 255, 255, 0.2);
    }
`;
